import { ModelSelect } from 'vue-search-select'
import { MultiSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import Authorization from "@/services/user/auth.service";
import GenderService from "@/services/common/gender.service";
import CountryService from "@/services/common/country.service";
import ClientService from "@/services/user/client.service";
import RolesService from '@/services/user/roles.service';
import ProductTypeService from '../../../services/common/product_type.service';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudClient",
    components: {
        ModelSelect,
        MultiSelect,
    },
    data() {
        return {
            showForm: true,
            info_message: '',
            error_message_text: 'Error enviando informacion. No se pudo crear el nuevo usuario',
            error_message: false,
            success_message: false,
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            load: false,
            showpass: false,
            samepass: true,
            repassword: '',
            countries: [],
            genders: [],
            roles: [],            
            formData: {
                first_name: '',
                last_name: '',
                phone: '',
                email: '',
                parent: '',
                parent_id: '',
                gender_id: '',
                gender: {
                    value: '',
                    text: ''
                },
                password: '',                
                //location
                address: '',
                city: '',
                province: '',
                zip_code: '',
                //agency
                iata: '',
                company: '',
                is_agent: false,                
                country_id: '',
                active: 0,
                verify: 0,
                country: {
                    value: '',
                    text: ''
                },
                roles: [],
                margin: []
            },
        }
    },
    created() {
        // Init country select
        let data =[]
        data.push(CountryService.getSimpleList())//.then(c => this.parents = c)
        data.push(RolesService.getSimpleList())//.then(c => this.countries = c)
        data.push(ProductTypeService.getCustomList())//.then(c => this.countries = c)
        this.clearForm()
        Promise.all(data).then(d => {            
            this.countries = d[0]
            this.roles = d[1]
            this.formData.margin = d[2]
            this.validateInitInfo()
        })  
        
        if(this.mode != 'create') {             
            this.initForm()
        }
        // Init gender select
        GenderService.getSimpleList().then(c => this.genders = c)
    },     
    methods: {   
        validateInitInfo() {
            if(this.roles.length == 0 || this.countries.length == 0 ) {
                this.showForm = false
                this.info_message = (this.roles.length == 0) ? 'Roles <br>'  : ''
                this.info_message += (this.countries.length == 0) ? ' Paises <br>' : ''                
            }
        },         
        initForm() {
            // For upd action
            if (this.mode != 'create') {
                ClientService.getClient(this.info).then(e => {        
                    this.clearForm()                         
                    this.formData.first_name = e.first_name
                    this.formData.last_name = e.last_name
                    this.formData.phone = e.phone
                    this.formData.email = e.email
                    this.formData.parent = (e.parent) ? e.parent.first_name + ' ' + e.parent.last_name : ''
                    this.formData.parent_id = (e.parent) ? e.parent.id : ''
                    this.formData.address = e.address
                    this.formData.city = e.city
                    this.formData.province = e.province
                    this.formData.zip_code = e.zip_code
                    this.formData.iata = e.i_a_t_a
                    this.formData.company = e.company
                    this.formData.is_agent = e.is_travel_agency
                    this.formData.gender_id = e.gender_id
                    this.formData.gender = e.gender_id
                    this.formData.country_id = e.country_id
                    this.formData.country = e.country_id                    
                    this.formData.active = e.active
                    this.formData.verify = e.verified
                    for(let r of e.roles) {
                        this.formData.roles.push({
                            text: r.role_name,
                            value: r.id
                        })
                    }   
                }).catch(err => {
                    console.log(err)
                    // this.$router.push({name:'Login'})  
                    this.$router.go(0)
                })
            } else {                
                if(Authorization.validateRole(['ROLE_Agency'], true)) {
                    this.formData.is_agent = true
                    this.formData.parent = localStorage.getItem('id')
                    this.formData.roles = this.roles.filter(role => role.text == 'ROLE_Vendor')                    
                }
            }
        },   
        comparePass() {
            this.samepass = (this.password != this.repassword)
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        }, 
        onRoleSelect(items, litem){                       
            this.formData.roles = items            
        },       
        submitForm(e) {
            e.preventDefault();            
            if(this.validate(this.formData.first_name) || this.validate(this.formData.last_name) || this.validate(this.formData.gender) ||
            this.validate(this.formData.email) || this.validate(this.formData.country)) {
                
                const data = {       
                    first_name: this.formData.first_name,
                    last_name: this.formData.last_name,
                    phone: this.formData.phone,
                    email: this.formData.email,
                    parent: this.formData.parent,
                    parent_id: (typeof this.formData.parent_id == 'object') ? this.formData.parent_id.value : this.formData.parent_id,
                    gender_id: (typeof this.formData.gender == 'object') ? this.formData.gender.value : this.formData.gender,
                    password: this.formData.password,
                    address: this.formData.address,
                    city: this.formData.city,
                    province: this.formData.province,
                    zip_code: this.formData.zip_code,
                    iata: this.formData.iata,
                    company: this.formData.company,
                    is_agent: this.formData.is_agent,
                    country_id: (typeof this.formData.country == 'object') ? this.formData.country.value : this.formData.country,
                    active: this.formData.active,
                    verify: this.formData.verify,
                    roles: this.formData.roles,
                    margin: this.formData.margin,
                }
                if('id' in this.formData) data.id = this.formData.id
                ClientService.addUpdClient(data, (this.mode == 'create')).then(res => {                    
                    if(res) {
                        this.formData.password = ''
                        this.repassword = ''
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getClient(e) {
            console.log(e)
        },
        clearForm() {
            return {
                first_name: '',
                last_name: '',
                phone: '',
                email: '',
                parent: '',
                parent_id: '',
                gender_id: '',
                gender: {
                    value: '',
                    text: ''
                },
                password: '',                
                //location
                address: '',
                city: '',
                province: '',
                zip_code: '',
                //agency
                iata: '',
                company: '',
                is_agent: '',                
                country_id: '',
                country: {
                    value: '',
                    text: ''
                },
                roles: [],
                margin: []
            }
        }
    },
    watch: {
        mode() {
            this.initForm()
        },
        repassword() {
            this.samepass =  (this.repassword == this.formData.password || 
                (this.repassword == '' && this.formData.password == ''))
        },             
    },
    computed: {
        is_admin() {
            return Authorization.validateRole(['ROLE_Admin'])    
        },
        is_agency() {
            return Authorization.validateRole(['ROLE_Agency'], true)    
        },        
        is_vendor() {            
            return (Authorization.validateRole(['ROLE_Agency'], true) && localStorage.getItem('email') != this.formData.email)            
        }
    },
}
