import BookingStatusService from "@/services/booking/booking_status.service";
import BookingService from "@/services/booking/booking.service";
import { ModelSelect } from 'vue-search-select';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudBooking",
    components: {
        ModelSelect,
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,
            status: [],
            statusSelected: [],
            formData: {},
        }
    },
    created() {        
        BookingStatusService.getSimpleList().then(item => { this.statusSelected = item })        
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        },
        loadItems() {
            if('bk_items' in this.formData)
                return (this.formData.bk_items.length > 0)
            return false
        }
    }, 
    methods:{
        initForm() {
            // For upd action
            BookingService.getBooking(this.info).then(booking_items => {
                if(this.mode !== 'create') {
                    this.clearForm()
                    this.formData.id             = booking_items.id
                    this.formData.status         = booking_items.status
                    this.formData.name           = booking_items.pax[0].name
                    this.formData.email          = booking_items.pax[0].email
                    this.formData.phone          = booking_items.pax[0].phone
                    this.formData.description    = booking_items.pax[0].description
                    this.formData.import         = booking_items.import
                    this.formData.booking_number = booking_items.booking_number
                    this.formData.creation_date  = booking_items.creation_date                    

                    this.formData.bk_items = [];                    
                    if(booking_items.bk_items.length>0)
                        booking_items.bk_items.forEach(el => this.formData.bk_items.push(el))
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            e.stoppreventDefault
            
            const form = {
                id:         this.formData.id,
                name:       this.formData.name,
                email:      this.formData.email,
                phone:      this.formData.phone,
                status_id:  this.status.value,
            }
            
            if(this.validate(this.formData.name) || this.validate(this.formData.email) || this.validate(this.formData.phone)) {
                BookingService.addUpdBooking(form, (this.mode === 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 

                        const selectedStatus = this.statusSelected.find((el) => el.value === this.status.value);
                        if (selectedStatus) {
                            // Actualizo label en el form
                            this.formData.status.description = selectedStatus.text;
                            // Actualizo tabla
                            // Pendiente
                        }
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        async getBooking(e) {
            console.log(e)
        },
        clearForm() {
            this.formData = {
                //general info
                status: {},
                booking_number: '',
                creation_date: '',
                name: '',
                email: '',
                import: '',
                description: '',
                check_in_date: '',
                check_out_date: '',
                bk_items: [],
            }
        }
    }
}
