import BookingStatusService from "@/services/booking/booking_status.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudBookingStatus",
    components: {        
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,

            formData: {
                //general info
                id: '',
                name: '',
                description: '',
                background_color: '',
                color: '',
            },
        }
    },
    created() {
        // Init country select
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            BookingStatusService.getBookingStatus(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.name = e.name
                    this.formData.description = e.description
                    this.formData.background_color = e.background_color
                    this.formData.color = e.color
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();
            this.error_message = false
            this.success_message = true
            
            if(!this.validate(this.formData.name)){
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
            }
            console.log(this.formData)
            /* if(!this.validate(this.formData.codigo)){
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
            } */
            
            if(!this.error_message)
            {   
                BookingStatusService.addUpdBookingStatus(this.formData, (this.mode === 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            }
        },
        async getBookingStatus(e) {
            console.log(e)
        },
    }
}
