import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import PickupService from "@/services/car/pickup_places.service";
import CarCompanyServices from "@/services/car/car_company.service";
import PlaceServices from "@/services/common/place.service";

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudCarPickupPlaces",    
    components: {        
        ModelSelect,                
    },
    data: () => ({     
        success_message_text: 'Se salvaron los datos de la habitacin correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        load: false,     
        showForm: true,           
        pickup: 'AM',     
        dropoff: 'PM',   
        companies: [],  
        places: [],  
        formData: {},        
    }),
    created() { 
        const data = []
        data.push(CarCompanyServices.getSimpleList())
        data.push(PlaceServices.getSimpleList())
        Promise.all(data).then(d => {
            // console.log(d)
            this.companies = d[0]
            this.places = d[1]
            this.validateInitInfo()            
        })  
        if(this.mode != 'create') {   
            this.initForm() 
        }
    },    
    computed: {
        hours() {
            const hours = []
            for(let i=1; i<=12; i++){
                hours.push(i + ':00')
            }
            return hours
        }
    },
    methods:{     
        validateInitInfo() {
            if(this.companies.length == 0) {
                this.showForm = false
                this.info_message = (this.companies.length == 0) ? 'Compaas <br>'  : ''
            }
        },   
        initForm() {
            PickupService.getPickupPlace(this.info).then(e => {
                this.clearForm()

                if(this.mode != 'create') {                    
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.address = e.address
                    this.formData.company_id = e.car_company.id
                    this.formData.place_type_id = e.places_type.id                    
                    this.formData.pickup_time = this.parseTime(e.pickup_time).time
                    this.formData.dropoff_time = this.parseTime(e.dropoff_time).time
                    this.formData.active = e.active
                    this.pickup = this.parseTime(e.pickup_time).part_day
                    this.dropoff = this.parseTime(e.dropoff_time).part_day
                }
            })
        },
        validate(value) {            
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            else if(typeof value == 'array')
                if(value.length == 0)
                    return false
            return true
        },        
        submitForm(e) {            
            e.preventDefault();             
            if(this.validate(this.formData.alias) && this.validate(this.formData.address)) {   
                this.load = true

                const data = {                    
                    alias: this.formData.alias,
                    address: this.formData.address,    
                    car_company_id: (typeof this.formData.company_id == 'object') ? this.formData.company_id.value : this.formData.company_id,            
                    place_type_id: (typeof this.formData.place_type_id == 'object') ? this.formData.place_type_id.value : this.formData.place_type_id,            
                    pickup_time: this.formData.pickup_time + ' ' + this.pickup,
                    dropoff_time: this.formData.dropoff_time + ' ' + this.dropoff,                
                    active: 1,
                }
                if('id' in this.formData) data.id = this.formData.id

                PickupService.addUpdPickupPlace(data, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(this.mode == 'create')
                        this.clearForm()
                    if(!res) {                         
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando el lugar'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);                                                
                    } else {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false                            
                        }, 3000); 
                    }
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }            
        },        
        clearForm() {
            this.formData = {                
                alias: '',
                address: '', 
                company_id: {
                    value: 0,
                    text: ''
                },
                place_type_id: {
                    value: 0,
                    text: ''
                },               
                pickup_time: '9:00',
                dropoff_time: '5:00',                
                active: 1,
            }
        },
        parseTime(time) {
            const parse = time.split(' ')
            return {
                time: parse[0],
                part_day: parse[1],
            }
        }
    },
}
