import moment from 'moment'
import AddImages from '@/components/AddImages';
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import CarCategoryService from "@/services/car/car_category.service";

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudCarCategory",    
    components: {        
        ModelSelect,
        AddImages                
    },
    data: () => ({     
        success_message_text: 'Se salvaron los datos de la categora correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        load: false,     
        showForm: true, 
        img: {
            id: 0,
            class_name: 'car',            
            id_object_class: 0,
            subcategory: 'Categoria',            
            lang: 'all',
            image_name: '',
            image_keyword: '',
            url: '',
            file: '',
            ext: '',
            anno: moment().format('YYYY'),
            order: 1,
            active: 1,
        },          
        formData: {},        
    }),
    created() { 
        this.initForm()
    },        
    methods:{   
        initGallery(images) {
            this.galleries = []            
            for(let img of images) {
                const original = img.resources.filter(i => i.image == 'original')    
                this.galleries.push({
                    id: img.id,
                    class_name: img.class_name,            
                    id_object_class: img.id_object_class,
                    subcategory: img.subcategory,
                    lang: img.lang,
                    image_name: img.image_keyword,
                    image_keyword: img.image_keyword,
                    url: original[0].url,
                    file: {},
                    ext: img.ext,
                    anno: img.anno,
                    order: img.order,
                    active: img.active,
                })
            }
            if(this.galleries.length > 0)
                this.img = this.galleries[0]
        },       
        initForm() {
            CarCategoryService.getCarCategory(this.info).then(e => {
                this.clearForm()

                if(this.mode != 'create') {                    
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.description = e.description
                    this.formData.fuel = e.fuel
                    this.formData.active = e.active
                    if(e.images)    
                        this.initGallery(e.images)
                }
            })
        },
        validate(value) {            
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            else if(typeof value == 'array')
                if(value.length == 0)
                    return false
            return true
        },        
        submitForm(e) {            
            e.preventDefault();             
            if(this.validate(this.formData.alias) && this.validate(this.formData.description)) {   
                this.load = true

                const data = {                                        
                    alias: this.formData.alias,
                    description: this.formData.description,
                    fuel: this.formData.fuel,
                    active: this.formData.active,
                }
                if('id' in this.formData) data.id = this.formData.id

                const fd = new FormData()  
                
                if(this.img.url.includes('blob')) {                    
                    fd.append('images', this.img.file, this.img.file.name)
                } else 
                    fd.append('images', this.img.id)
                fd.append('imgOrder', [1])
                fd.append('subcategory', [this.img.subcategory])
                // agrego los otros campos
                
                for(let field of Object.keys(data)) {
                    fd.append(field, data[field])
                }
                CarCategoryService.addUpdCarCategory({form: fd, id: data.id}, (this.mode == 'create')).then(res => {                
                // CarCategoryService.addUpdCarCategory({form: formData, id: data.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(this.mode == 'create')
                        this.clearForm()
                    if(!res) {                         
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando el categora'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);                                                
                    } else {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 
                    }
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }            
        },  
        updImage(obj) {
            if(obj.index >= 0) {
                this.img = obj.img
                this.img.id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },      
        clearForm() {
            this.formData = {                                
                alias: '',
                description: '',
                fuel: 0,
                active: 1
            }
        }
    },
}
