import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import RateTypeService from "@/services/rate/rate_type.service";
import ProductTypeService from "@/services/common/product_type.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudRateType",
    components: {        
        ModelSelect
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,  
            product_types: [],
            formData: {},
        }
    },
    created() {
        // Init country select
        ProductTypeService.getSimpleList().then(p => {this.product_types = p})
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            RateTypeService.getRateType(this.info).then(e => {
                this.clearForm()
                if(this.mode != 'create') {
                    this.formData.name = e.name
                    this.formData.active = e.active
                    this.formData.description = e.description                    
                    this.formData.product_type_id = e.product_type.id
                    this.formData.id = e.id
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();                  
            if(this.validate(this.formData.name) ) {                             
                this.load = true
                const data = {                    
                    name: this.formData.name,
                    product_type_id: (typeof this.formData.product_type_id == 'object') ? this.formData.product_type_id.value : this.formData.product_type_id,
                    description: this.formData.description,
                    active: (this.formData.active) ? 1 : 0,
                }
                
                if('id' in this.formData) data.id = this.formData.id

                RateTypeService.addUpdRateType(data, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        async getRateType(e) {
            console.log(e)
        },
        clearForm() {
            this.formData = {
                active: '',
                name: '',
                description: '',
                product_type_id: {
                    value: 0,
                    text: '',
                }
            }
        },
    }
}
