import moment from 'moment'
import MarketplaceItemService from "@/services/marketplace/marketplace_item.service";
import MarketplaceCategoryService from "@/services/marketplace/marketplace_category.service";


import { MultiSelect } from 'vue-search-select';
import AddImages from '@/components/AddImages';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudMarketplaceItem",
    components: {
        AddImages,
        MultiSelect,
    },
    data() {
        return {
            success_message_text: 'La informacin ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            categoriesSelected: [],
            load: false,
            img: {},
            formData: {
                //general info
                // id: '',
                alias: '',
                keyword: '',
                description: '',
                margin: '',
                price: '',
                features: '',
                images: [],
                categories: [],
                active: true,
            },
        }
    },
    created() {
        MarketplaceCategoryService.getSimpleList().then(item => { this.categoriesSelected = item })
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            MarketplaceItemService.getMarketplaceItem(this.info)
            .then(item => {
                if(this.mode != 'create') {
                    this.formData.id = item.id
                    this.formData.alias = item.alias
                    this.formData.description = item.description
                    this.formData.keyword = item.keyword
                    this.formData.margin = item.margin
                    this.formData.price = item.price
                    this.formData.features = item.features
                    this.formData.active = item.active
                    this.formData.categories = [];                    
                    // categorias
                    if(item.marketplace_categories.length>0)
                        item.marketplace_categories.forEach(el => this.formData.categories.push(
                            {
                                value: el.id,
                                text: el.alias,
                            }
                        ))
                    // console.log(this.formData.categories)
                    // imagen
                    if(item.images)    
                        this.initGallery(item.images)
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        updImage(obj) {
            if(obj.index >= 0) {
                this.img = obj.img
                this.img.id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },
        initGallery(images) {
            // this.galleries = []
            for(let img of images) {
                const original = img.resources.filter(i => i.image == 'original')                
                this.img = {
                    id: img.id,
                    class_name: img.class_name,            
                    id_object_class: img.id_object_class,
                    subcategory: img.subcategory,
                    lang: img.lang,
                    image_name: img.image_keyword,
                    image_keyword: img.image_keyword,
                    url: original[0].url,
                    file: {},
                    ext: img.ext,
                    anno: img.anno,
                    order: img.order,
                    active: img.active,
                }
            }
            
        },        
        submitForm(e) {            
            e.preventDefault();
            this.error_message = false
            this.success_message = true
            
            if(!this.validate(this.formData.alias) ||
            //    !this.validate(this.formData.description) ||
               !this.validate(this.formData.keyword) ||
            //    !this.validate(this.formData.margin) ||
               !this.validate(this.formData.price) ||
            //    !this.validate(this.formData.features) ||
               !this.formData.categories.length>0
            ){
                this.error_message = true
                this.success_message = false 
                this.error_message_text = 'Faltan datos por llenar'
            }
            const data = {                                        
                    alias: this.formData.alias,
                    description: this.formData.description,
                    active: (this.formData.active) ? 1 : 0,
                    keyword: this.formData.keyword,
                    margin: this.formData.margin ? this.formData.margin : 0,
                    price: this.formData.price,
                    features: this.formData.features,
            }

            data.categories = this.formData.categories.map(el => el.value)
            

            if('id' in this.formData) data.id = this.formData.id
            const fd = new FormData()  
            
            if(!!this.img){
                if(this.img.url.includes('blob')) {                    
                    fd.append('images', this.img.file, this.img.file.name)
                } else 
                    fd.append('images', this.img.id)
                fd.append('imgOrder', [1])
                fd.append('subcategory', [this.img.subcategory])
                // agrego los otros campos
            }
            
            for(let field of Object.keys(data)) {
                fd.append(field, data[field])
            }
            
            if(!this.error_message)
            {   
                this.load = true
                MarketplaceItemService.addUpdMarketplaceItem({form: fd, id: data.id}, (this.mode === 'create'))
                .then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 
                    } else {
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando el producto'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);    
                    }
                })
            }
        },
        async getMarketplaceItem(e) {
            console.log(e)
        },
        onCategorySelect(items, litem){
            this.formData.categories = items
            // console.log(items)
        },
    }
}
