import CarCompanyService from "@/services/car/car_company.service";

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudCarCopany",    
    data: () => ({     
        success_message_text: 'Se salvaron los datos de la categora correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        load: false,     
        showForm: true,           
        formData: {},        
    }),
    created() { 
        this.initForm()
    },        
    methods:{        
        initForm() {
            CarCompanyService.getCarCompany(this.info).then(e => {
                this.clearForm()

                if(this.mode != 'create') {                    
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                    this.formData.phone = e.phone
                    this.formData.active = e.active
                }
            })
        },
        validate(value) {            
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            else if(typeof value == 'array')
                if(value.length == 0)
                    return false
            return true
        },        
        submitForm(e) {            
            e.preventDefault();             
            if(this.validate(this.formData.alias)) {   
                this.load = true

                const data = {                                        
                    alias: this.formData.alias,
                    phone: this.formData.phone,
                    active: this.formData.active,
                }
                if('id' in this.formData) data.id = this.formData.id
                
                CarCompanyService.addUpdCarCompany(data, (this.mode == 'create')).then(res => {
                    this.load = false
                    this.clearForm()
                    if(!res) {                         
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando la compaa'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);                                                
                    } else {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 
                    }
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }            
        },        
        clearForm() {
            this.formData = {                                
                alias: '',
                phone: '',
                active: 1
            }
        },
    },
}
